.Services {
  font-family: "Roboto";
  font-style: normal;
  width: 96%;
  border-radius: 25px;
  max-width: 1400px;
  
  display: flex;
  flex-direction: column;

  &__title {
    font-weight: 700;
    font-size: 70px;
    line-height: 82px;
    text-align: left;
  }

  &__discription {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    max-width: 800px;
    text-align: left;
  }

  &__lists {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(auto-fit, minmax(412px, 412px));
    list-style-type: none;
    padding: 0;
  }


  &__lists-item {
    text-align: left;
  }

  &__lists-title {
    font-weight: 700;
font-size: 24px;
line-height: 28px;

  }

  &__lists-description {
    font-weight: 400;
font-size: 16px;
line-height: 24px;
color: #737373;

  }


}
